--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
  color: royalblue; }
  a:visited {
    color: purple; }
  a:hover, a:focus, a:active {
    color: midnightblue; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
#main-navigation {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: -1;
  transition: transform 0.5s ease-in-out;
  transform: translateY(-9999em); }
  @media screen and (min-width: 75em) {
    #main-navigation {
      transform: none !important;
      position: relative;
      z-index: 1;
      margin-left: auto; }
      #main-navigation > ul {
        display: flex; }
        #main-navigation > ul > li {
          margin: 0 0.5rem; } }
  #main-navigation.active {
    transform: translateY(0) !important; }
    @media screen and (min-width: 75em) {
      #main-navigation.active {
        transform: none !important; } }
  #main-navigation > ul > li > ul {
    max-height: 0;
    overflow: hidden;
    transition: max-height .25s; }
    @media screen and (min-width: 75em) {
      #main-navigation > ul > li > ul {
        max-height: none;
        transition: opacity 0.375s;
        opacity: 0; } }
  #main-navigation ul {
    list-style: none;
    margin: 0; }
    #main-navigation ul ul {
      z-index: 99999; }
      @media screen and (min-width: 75em) {
        #main-navigation ul ul {
          position: absolute;
          top: 100%;
          left: -999em; } }
      #main-navigation ul ul ul {
        left: -999em;
        top: 0; }
      #main-navigation ul ul li:hover > ul,
      #main-navigation ul ul li.focus > ul {
        left: 100%; }
      #main-navigation ul ul a {
        text-transform: none;
        letter-spacing: normal; }
        #main-navigation ul ul a, #main-navigation ul ul a:hover {
          color: #FFF; }
        @media screen and (min-width: 75em) {
          #main-navigation ul ul a {
            margin-top: 0;
            margin-bottom: 0;
            padding: 0.75rem;
            width: 200px;
            font-size: 0.9375em;
            background: #394ba0;
            border: none !important; }
            #main-navigation ul ul a:hover {
              transition: background 0.375s;
              background: #ff7439; } }
      #main-navigation ul ul current_page_item > a,
      #main-navigation ul ul .current-menu-item > a {
        background: #ff7439; }
        @media screen and (min-width: 75em) {
          #main-navigation ul ul current_page_item > a:hover,
          #main-navigation ul ul .current-menu-item > a:hover {
            color: #FFF; } }
    #main-navigation ul li:hover > ul,
    #main-navigation ul li.focus > ul {
      left: auto;
      opacity: 1; }
  #main-navigation li {
    position: relative; }
  #main-navigation a {
    display: block;
    padding: 0.5rem 0.5rem 0.5rem 3.5rem;
    background: #394ba0;
    color: #FFF;
    text-transform: uppercase;
    font-size: 1.125em;
    letter-spacing: 0.0625em;
    text-decoration: none; }
    @media screen and (min-width: 75em) {
      #main-navigation a {
        padding: 0.25rem 0;
        margin: 0.5rem;
        color: #000;
        background: transparent;
        border-bottom: 6px solid #FFF; }
        #main-navigation a:hover {
          color: #ff7439; } }
  #main-navigation .current_page_item > a,
  #main-navigation .current-menu-item > a {
    background: #ff7439; }
    #main-navigation .current_page_item > a:hover,
    #main-navigation .current-menu-item > a:hover {
      cursor: text; }
    @media screen and (min-width: 75em) {
      #main-navigation .current_page_item > a,
      #main-navigation .current-menu-item > a {
        background: transparent;
        border-bottom: 6px solid #000; }
        #main-navigation .current_page_item > a:hover,
        #main-navigation .current-menu-item > a:hover {
          color: #000; } }
  #main-navigation .arrow {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    padding: 0;
    width: 55px;
    height: 55px;
    background: none;
    color: #FFF;
    display: flex;
    justify-content: center;
    align-items: center; }
    @media screen and (min-width: 75em) {
      #main-navigation .arrow {
        display: none; } }
    #main-navigation .arrow:after {
      content: '\2228';
      display: block;
      margin-top: -12px;
      transition: transform 0.25s, margin-top 0.25s; }
    #main-navigation .arrow.active:after {
      transform: rotate(180deg);
      margin-top: -2px; }

/* Small menu. */
.mobile-menu-toggle {
  position: relative;
  padding: 0;
  width: 35px;
  height: 28px;
  margin-left: auto; }
  @media screen and (min-width: 75em) {
    .mobile-menu-toggle {
      display: none; } }
  .mobile-menu-toggle, .mobile-menu-toggle:hover {
    background: none; }
  .mobile-menu-toggle .inner, .mobile-menu-toggle:before, .mobile-menu-toggle:after {
    content: '';
    display: block;
    width: 100%;
    height: 4px;
    position: absolute;
    background: #000;
    transition: all 0.25s; }
  .mobile-menu-toggle:before {
    top: 0; }
  .mobile-menu-toggle:after {
    top: 100%;
    margin-top: -4px; }
  .mobile-menu-toggle .inner {
    top: 50%;
    margin-top: -2px; }
  .mobile-menu-toggle.active:before {
    transform: rotate(45deg);
    top: 45%; }
  .mobile-menu-toggle.active:after {
    transform: rotate(-45deg);
    top: 45%;
    margin-top: 0; }
  .mobile-menu-toggle.active .inner {
    opacity: 0; }

