    max-width: 100%; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5em; }

.updated:not(.published) {
  display: none; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5em 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5em; }

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
  word-wrap: break-word; }

.bypostauthor {
  display: block; }

.wrapper {
  padding-left: 1.875rem;
  padding-right: 1.875rem; }
  @media screen and (min-width: 46.875em) {
    .wrapper {
      padding-left: 2.25rem;
      padding-right: 2.25rem; } }
  @media screen and (min-width: 75em) {
    .wrapper {
      padding-left: 3.5rem;
      padding-right: 3.5rem; } }

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
  /* Theme Footer (when set to scrolling) */
  display: none; }

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
  display: block; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
  display: inline-block; }

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
  margin-bottom: 1.5em;
  max-width: 100%; }
  .wp-caption img[class*="wp-image-"] {
    display: block;
    margin-left: auto;
    margin-right: auto; }
  .wp-caption .wp-caption-text {
    margin: 0.8075em 0; }

.wp-caption-text {
  text-align: center; }

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
  margin-bottom: 1.5em; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

/*--------------------------------------------------------------
# Pages
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Home Pge
--------------------------------------------------------------*/
.hero {
  position: relative;
  width: 100%;
  overflow: hidden;
  padding-top: 2rem;
  padding-bottom: 2rem; }
  .hero:after {
    content: '';
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    opacity: .7; }
  .hero img {
    object-fit: cover;
    font-family: "object-fit: cover";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1; }
  .hero .services-container {
    position: relative;
    z-index: 300; }
    .hero .services-container ul {
      margin: 1.5em 2.3em;
      padding-left: 10px;
      padding-right: 10px; }
  .hero .callout-container {
    position: relative;
    z-index: 300;
    padding-left: 1.875rem;
    padding-right: 1.875rem; }
    @media screen and (min-width: 46.875em) {
      .hero .callout-container {
        padding-left: 2.25rem;
        padding-right: 2.25rem; } }
    @media screen and (min-width: 75em) {
      .hero .callout-container {
        padding-left: 3.5rem;
        padding-right: 3.5rem; } }

.home #main .hentry {
  margin: 0; }

.home #main article {
  position: relative; }
  .home #main article:after {
    content: '';
    position: absolute;
    z-index: 100;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #394ba0;
    opacity: .75; }
  .home #main article .home-main-content-img {
    object-fit: cover;
    font-family: "object-fit: cover";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1; }
  .home #main article .wrapper {
    position: relative;
    z-index: 300;
    color: #fff;
    padding-top: 2rem;
    padding-bottom: 2rem; }

.home .call-to-action .inner-wrapper {
  position: relative; }
  .home .call-to-action .inner-wrapper a {
    color: #fff;
    text-decoration: none; }
  .home .call-to-action .inner-wrapper img {
    object-fit: cover;
    font-family: "object-fit: cover";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    display: block; }
  .home .call-to-action .inner-wrapper .title-container {
    padding-left: 1.875rem;
    padding-right: 1.875rem;
    padding-top: 10em;
    padding-bottom: 10em; }
    @media screen and (min-width: 46.875em) {
      .home .call-to-action .inner-wrapper .title-container {
        padding-left: 2.25rem;
        padding-right: 2.25rem; } }
    @media screen and (min-width: 75em) {
      .home .call-to-action .inner-wrapper .title-container {
        padding-left: 3.5rem;
        padding-right: 3.5rem; } }
  .home .call-to-action .inner-wrapper span {
    display: inline-block; }
  .home .call-to-action .inner-wrapper .title {
    border-top: 2px solid #ff7439;
    font-size: 2em;
    width: auto;
    margin: auto; }

